import DigitalRiverPaymentRequest from './DigitalRiverPaymentRequest';
import {getPaymentMethods} from './fetch-payment-methods';
import {
  registerRedirectOnComplete,
  runClientProvidedCancelEvent,
  runClientProvidedCompleteEvents,
  sendRedirectSourceData
} from './dropin-events';
import {createCssSpinner} from './css-class-utils';
import {
  getLocaleMessage
} from '../app/components/localization/localized-messages';
import {getActiveAcceptance} from './complianceData';
import {
  getAddNewPaymentText,
  getMustAcceptTermsText,
  getSaveCardMandateText
} from './mandate';
import {getCurrencyFromCountry} from './country-currency-converter';
import {getRedirectReceiverURL} from './url-utils';
import {determineButtonConfigurationText} from './dropin/button-text-configuration';
import {createApplyNowButton, getMSTSEnrollmentURL} from './msts/msts-ui';
import {
  addChangeEventToCheckbox,
  createTermsOfSale,
  createTermsSourceObject,
  displayShowTermsOfSaleDisclosure,
  getTermsCheckboxValue,
  isTermsOfSaleDisclosureMode
} from './dropin/terms';
import {LOGGING_APPLICATION_KEY} from '../app/key-logging';
import {openWindow} from '../app/components/controller/controller-window-opener';

function getLocalizedPaymentMethodName(defaultName, paymentMethodType, locale) {
  const localizedPaymentMethod = getLocaleMessage(locale, paymentMethodType);
  return typeof localizedPaymentMethod !== 'undefined' ? localizedPaymentMethod : defaultName;
}

function supportedPaymentMethods(locale) {
  const supportedPaymentMethods = [
    {name: 'Online Banking', code: 'onlinebanking', type: 'onlineBanking', needsPayButton: true, standaloneButton: false, onlyButton: false, requiresSessionInformation: true},
    {name: 'Credit Card', code: 'creditcard', type: 'creditCard', needsPayButton: true, standaloneButton: false, onlyButton: false, hasLogos: true, defaultStyle: getCreditCardDefaultStyle()},
    {name: 'Google Pay', code: 'googlepay', type: 'googlePay', needsPayButton: false, standaloneButton: true, onlyButton: false},
    {name: 'Apple Pay', code: 'applepay', type: 'applePay', needsPayButton: false, standaloneButton: true, onlyButton: false},
    {name: 'PayPal', code: 'paypal', type: 'payPal', needsPayButton: false, standaloneButton: true, onlyButton: false, addSourceDataToOptions: true, defaultStyle: getPayPalDefaultStyle()},
    {name: 'PayPal Billing', code: 'paypal', type: 'payPalBilling', needsPayButton: false, standaloneButton: true, onlyButton: false, addSourceDataToOptions: true, defaultStyle: getPayPalDefaultStyle()},
    {name: 'PayPal Credit', code: 'paypalcredit', type: 'payPalCredit', needsPayButton: false, standaloneButton: true, onlyButton: false, addSourceDataToOptions: true, defaultStyle: getPayPalCreditDefaultStyle()},
    {name: 'Konbini', code: 'konbini', type: 'konbini', needsPayButton: true, standaloneButton: false, onlyButton: false},
    {name: 'Wire Transfer', code: 'wiretransfer', type: 'wireTransfer', needsPayButton: true, standaloneButton: false, onlyButton: true, justRequiresSource: true},
    {name: 'bPay', code: 'bpay', type: 'bPay', needsPayButton: true, standaloneButton: false, onlyButton: true, justRequiresSource: true},
    {name: 'Alipay', code: 'alipay', type: 'alipay', needsPayButton: true, standaloneButton: false, onlyButton: true},
    {name: 'Bank Transfer', code: 'banktransfer', type: 'bankTransfer', needsPayButton: true, standaloneButton: false, onlyButton: true},
    //{name: 'Credit Installment', code: 'creditinstallment', type: 'creditInstallment', needsPayButton: true, standaloneButton: false, onlyButton: true}, TODO Enable when complete
    {name: 'Direct Debit', code: 'directdebit', type: 'directDebit', needsPayButton: true, standaloneButton: false, onlyButton: true},
    {name: 'Payco', code: 'payco', type: 'payco', needsPayButton: true, standaloneButton: false, onlyButton: true},
    {name: 'Klarna Credit', code: 'klarnacredit', type: 'klarnaCredit', needsPayButton: true, standaloneButton: false, onlyButton: true, needsActiveAcceptance: true},
    {name: 'Klarna Credit Recurring', code: 'klarnacreditrecurring', type: 'klarnaCreditRecurring', needsPayButton: true, standaloneButton: false, onlyButton: true, needsActiveAcceptance: true},
    //{name: 'Direct Debit GB', code: 'directdebitgb', type: 'directDebitGb', needsPayButton: true, standaloneButton: false, onlyButton: true},
    {name: 'Japan COD', code: 'codjapan', type: 'codJapan', needsPayButton: true, standaloneButton: false, onlyButton: true, justRequiresSource: true},
    {name: 'CC Avenue', code: 'ccavenue', type: 'ccavenue', needsPayButton: true, standaloneButton: false, onlyButton: true},
    {name: 'InvoiceMe', code: 'msts', type: 'msts', needsPayButton: true, standaloneButton: false, onlyButton: false, addSourceDataToOptions: true, needsActiveAcceptance: true, addApplyNowButton: true},
  ];

  return supportedPaymentMethods
    .map( paymentMethod => Object.assign({}, paymentMethod, {name: getLocalizedPaymentMethodName(paymentMethod.name, paymentMethod.type, locale)} ));

}

function getCreditCardDefaultStyle() {
  return {
    mask: true
  };
}

function getPayPalDefaultStyle() {
  return {
    label: 'checkout',
    size: 'responsive',
    color: 'gold',
    shape: 'rect',
    layout: 'horizontal',
    fundingicons: 'false',
    tagline: 'false'
  };
}

function getPayPalCreditDefaultStyle() {
  return {
    label: 'credit',
    size: 'responsive',
    shape: 'rect',
    layout: 'horizontal',
    tagline: 'false'
  };
}

function paymentMethodNotSupported(paymentMethod) {
  return !paymentMethod;
}

function runUpdateWith(event, data) {
  if (typeof event.updateWith !== 'undefined') {
    event.updateWith(data);
  }
}

export function addStandaloneButtonOptions(component, configuration, paymentMethod, paymentMethodFromAPI) {
  component.on('source', (event) => {
    if (typeof event.complete !== 'undefined') {
      event.complete('success');
    }
    runClientProvidedCompleteEvents(configuration, event, null, null, paymentMethodFromAPI);
  });

  component.on('click', (event) => {
    runUpdateWith(event, {});
  });

  component.on('shippingaddresschange', (event) => {
    runUpdateWith(event, {
      status: 'success'
    });
  });

  component.on('shippingoptionchange', (event) => {
    runUpdateWith(event, {});
  });

  component.on('cancel', () => {
    runClientProvidedCancelEvent(configuration, paymentMethod.type);
  });

}

export function getSessionPaymentRequest(configuration, sessionInformation, providedStyle, locale) {

  const orderTotalLabel = getLocaleMessage(locale, 'orderTotal');

  const style = typeof providedStyle !== 'undefined' ? providedStyle : {
    buttonType: 'plain',
    buttonColor: 'light',
    buttonLanguage: 'en'
  };

  return new DigitalRiverPaymentRequest({
    sessionId: configuration.sessionId,
    country: sessionInformation.country,
    currency: sessionInformation.currency,
    total: {
      label: orderTotalLabel,
      amount: parseFloat(sessionInformation.amount),
    },
    style: style,
    billingAddress: configuration.billingAddress,
    requestShipping: false
  });
}

function isPaymentRequestUsed(paymentMethod) {
  return paymentMethod.code === 'googlepay' || paymentMethod.code === 'applepay';
}

export function addSourceDataToOptions(configuration, componentOptionsOrPaymentRequest, paymentMethod, controller, availablePaymentMethod, sessionInformation, locale) {
  if (typeof componentOptionsOrPaymentRequest.style === 'undefined' && typeof paymentMethod.defaultStyle !== 'undefined') {
    componentOptionsOrPaymentRequest.style = paymentMethod.defaultStyle;
  }

  componentOptionsOrPaymentRequest.sourceData = createSourceData(configuration, paymentMethod, controller.id, availablePaymentMethod, sessionInformation, locale);

  return componentOptionsOrPaymentRequest;
}

function getComponentOptions(componentOptionsKey, configuration) {
  return typeof componentOptionsKey !== 'undefined'
  && typeof configuration.paymentMethodConfiguration !== 'undefined'
  && typeof configuration.paymentMethodConfiguration[componentOptionsKey] !== 'undefined' ? configuration.paymentMethodConfiguration[componentOptionsKey] : {};
}

function shouldUseDefaultStyles(paymentMethod, componentOptionsOrPaymentRequest, configuration) {
  return !isPaymentRequestUsed(paymentMethod) &&
    typeof componentOptionsOrPaymentRequest.style === 'undefined' &&
    typeof configuration.paymentMethodConfiguration !== 'undefined' &&
    typeof configuration.paymentMethodConfiguration.style !== 'undefined';
}

function createOnReadyResponse(paymentMethodResponse) {
  const types = typeof paymentMethodResponse.paymentMethods !== 'undefined' ?  paymentMethodResponse.paymentMethods.filter(pm => pm.addedToDOM === true).map(paymentMethod => paymentMethod.type) : [];
  return {
    paymentMethodTypes: types
  };
}

function paymentMethodsReturned(paymentMethodResponse) {
  return typeof paymentMethodResponse.paymentMethods !== 'undefined' && paymentMethodResponse.paymentMethods.length !== 0;
}

export function getFlow(configuration) {
  return typeof configuration.options !== 'undefined' && typeof configuration.options.flow !== 'undefined' ? configuration.options.flow : '';
}

export function getUsage(configuration) {
  return typeof configuration.options !== 'undefined' && typeof configuration.options.usage !== 'undefined' ? configuration.options.usage : null;
}

export function getSupportStorageForAPICall(configuration) {
  return getFlow(configuration) === 'managePaymentMethods' ? true : null;
}

export function isManagePaymentMethodsMode(configuration) {
  return getFlow(configuration) === 'managePaymentMethods';
}

export function isRedirectDisabled(configuration) {
  return typeof configuration.options !== 'undefined' && typeof configuration.options.redirect !== 'undefined' && typeof configuration.options.redirect.disableAutomaticRedirects !== 'undefined' ? configuration.options.redirect.disableAutomaticRedirects : false;
}

function getReturnUrl(configuration) {
  return configuration.options.redirect.returnUrl;
}

function getCancelUrl(configuration) {
  return configuration.options.redirect.cancelUrl;
}

export function isRedirectReturnUrlProvided(configuration) {
  return typeof configuration.options !== 'undefined' && typeof getReturnUrl(configuration)  !== 'undefined';
}

export function isRedirectCancelUrlProvided(configuration) {
  return typeof configuration.options !== 'undefined' && typeof getCancelUrl(configuration)  !== 'undefined';
}

export function getRecurringModel(configuration) {
  const CARD_ON_FILE = 'COF';
  if(isManagePaymentMethodsMode(configuration)) {
    return typeof configuration.options !== 'undefined' && typeof configuration.options.recurringModel !== 'undefined' ? configuration.options.recurringModel : CARD_ON_FILE;
  } else {
    return null;
  }
}

export function displaySavePaymentAgreement(configuration, paymentMethodFromAPI) {
  return typeof configuration.options !== 'undefined' && configuration.options.showSavePaymentAgreement && doesPaymentMethodSupportStorage(paymentMethodFromAPI);
}

function doesPaymentMethodSupportStorage(paymentMethod) {
  return typeof paymentMethod !== 'undefined' && paymentMethod.supportsStorage === true;
}


function filterOnlySupportsStorage(paymentMethodsFromAPI) {
  return paymentMethodsFromAPI.filter( pm => pm.supportsStorage === true);
}

function configurationAllowsPaymentMethod(paymentMethod, configuration) {

  if (typeof configuration.paymentMethodConfiguration !== 'undefined' &&
    typeof configuration.paymentMethodConfiguration.enabledPaymentMethods !== 'undefined' &&
    configuration.paymentMethodConfiguration.enabledPaymentMethods.length > 0) {

    return configuration.paymentMethodConfiguration.enabledPaymentMethods.includes(paymentMethod.type);

  }

  if (typeof configuration.paymentMethodConfiguration !== 'undefined' &&
    typeof configuration.paymentMethodConfiguration.disabledPaymentMethods !== 'undefined' &&
    configuration.paymentMethodConfiguration.disabledPaymentMethods.length > 0) {

    return !configuration.paymentMethodConfiguration.disabledPaymentMethods.includes(paymentMethod.type);

  }

  return true;
}

function paymentMethodsAdded(paymentMethodResponse) {
  return paymentMethodResponse.paymentMethods.filter(pm => pm.addedToDOM === true).length > 0;
}

function checkAndSendOnReadyEvent(configuration, paymentMethodResponse) {
  if (typeof configuration.onReady !== 'undefined') {
    configuration.onReady(createOnReadyResponse(paymentMethodResponse));
  }
}

function addMSTSEnrollmentURL(paymentMethod, configuration) {
  return paymentMethod.addApplyNowButton && getMSTSEnrollmentURL(configuration).length > 0;
}

function getMandateCheckboxDOMElement(paymentMethod) {
  return document.getElementById('DR-mandate-checkbox-' + paymentMethod.type);
}

function requiresUpdateToInternalSourceData(paymentMethod, configuration, availablePaymentMethod) {
  return paymentMethod.addSourceDataToOptions && displaySavePaymentAgreement(configuration, availablePaymentMethod);
}

function allPaymentMethodsFailedToAdd(paymentMethodResponse) {
  return !paymentMethodsAdded(paymentMethodResponse);
}

function onlyButtonsWereAdded(componentsReadyStatus) {
  return Object.keys(componentsReadyStatus).length === 0;
}

export function mountDropin(controller, configuration, createSource, createElement, instanceOptions, getComplianceDetails) {
  return function (node) {
    const locale = instanceOptions.locale;

    if (typeof node === 'string') {
      node = document.getElementById(node);
    }

    if (node instanceof HTMLElement === false) {
      throw new Error('mount() requires a valid HTMLElement or id attribute.');
    }

    const accordionId = 'dr-accordion-'+controller.id;

    const accordion = document.createElement('div');
    accordion.id = accordionId;
    node.appendChild(accordion);

    const isSupportStorageMode = isManagePaymentMethodsMode(configuration);
    const recurringModel = getRecurringModel(configuration);

    const buttonText = getButtonText(configuration, locale);

    const country = configuration.hasOwnProperty('billingAddress') && configuration.billingAddress.hasOwnProperty('address') && typeof configuration.billingAddress.address.country !== 'undefined' ? configuration.billingAddress.address.country : null;

    return getPaymentMethods(controller.id, configuration.sessionId, country, null, locale, getSupportStorageForAPICall(configuration), recurringModel).then(paymentMethodResponse => {

      const componentsMounted = {};
      const componentsReadyStatus = [];

      if (typeof paymentMethodResponse.paymentMethods === 'undefined' || paymentMethodResponse.paymentMethods.length === 0) {
        checkAndSendOnReadyEvent(configuration, paymentMethodResponse);
        return Promise.resolve(paymentMethodResponse);
      }

      registerRedirectOnComplete(controller, configuration, buttonText, paymentMethodResponse.paymentMethods);

      // Temporary
      if (isSupportStorageMode === true) {
        paymentMethodResponse.paymentMethods = filterOnlySupportsStorage(paymentMethodResponse.paymentMethods, supportedPaymentMethods(locale));
      }

      paymentMethodResponse.paymentMethods = paymentMethodResponse.paymentMethods.filter( pm => {
        return configurationAllowsPaymentMethod(pm, configuration);
      });

      const sessionInformation = paymentMethodResponse.sessionInformation;

      paymentMethodResponse.paymentMethods.forEach(availablePaymentMethod => {

        const paymentMethod = supportedPaymentMethods(locale).find(paymentMethod => paymentMethod.type === availablePaymentMethod.type);

        if (paymentMethodNotSupported(paymentMethod)) {
          return;
        }

        availablePaymentMethod.addedToDOM = true;

        const parent = document.createElement('div');
        parent.className = 'DR-card DR-' + paymentMethod.type;
        accordion.appendChild(parent);

        const bodyId = 'collapse-' + paymentMethod.type + '-' + controller.id;
        const headerId = 'heading-' + paymentMethod.type + '-' + controller.id;
        const submitButtonId = 'submit-' + paymentMethod.type + '-' + controller.id;
        const componentId = paymentMethod.type + '-' + controller.id;

        const shouldCreateBody = !paymentMethod.onlyButton;
        const {bodyParent, componentHolder} = createComponentContainer(paymentMethod, availablePaymentMethod, parent, bodyId, headerId, componentId, shouldCreateBody, accordionId, configuration, sessionInformation);

        if(paymentMethod.needsActiveAcceptance) {
          bodyParent.appendChild(createActiveAcceptance(paymentMethod, configuration, locale, sessionInformation));
        }

        if(isSupportStorageMode === true && !paymentMethod.standaloneButton) {
          bodyParent.appendChild(createMandate(paymentMethod, locale, componentHolder, isSupportStorageMode));
        } else if ((isSupportStorageMode === true || displaySavePaymentAgreement(configuration, availablePaymentMethod)) && paymentMethod.standaloneButton) {
          bodyParent.prepend(createMandate(paymentMethod, locale, componentHolder, isSupportStorageMode));
        } else if(displaySavePaymentAgreement(configuration, availablePaymentMethod) && !isSupportStorageMode) {
          bodyParent.appendChild(createMandate(paymentMethod, locale, componentHolder, isSupportStorageMode));
        }

        if (displayShowTermsOfSaleDisclosure(configuration, sessionInformation) && !paymentMethod.standaloneButton) {
          bodyParent.appendChild(createTermsOfSale(paymentMethod.type, locale, sessionInformation, paymentMethod, componentHolder));
        }
        if (displayShowTermsOfSaleDisclosure(configuration, sessionInformation) && paymentMethod.standaloneButton) {
          bodyParent.prepend(createTermsOfSale(paymentMethod.type, locale, sessionInformation, paymentMethod, componentHolder));
        }

        if (addMSTSEnrollmentURL(paymentMethod, configuration)) {
          const enrollmentUrl = getMSTSEnrollmentURL(configuration);
          bodyParent.appendChild(createApplyNowButton(enrollmentUrl, locale));
        }

        if (paymentMethod.needsPayButton) {
          bodyParent.appendChild(
            createSubmitButton(controller.id, configuration, createSource, componentsMounted, paymentMethod, submitButtonId, availablePaymentMethod, paymentMethodResponse.sessionInformation, locale)
          );
        }

        if (!paymentMethod.onlyButton) {

          const componentOptionsKey = findOptionsKey(configuration.paymentMethodConfiguration, paymentMethod);
          const componentOptions = getComponentOptions(componentOptionsKey, configuration);

          try {

            const componentOptionsOrPaymentRequest = isPaymentRequestUsed(paymentMethod) ?
              getSessionPaymentRequest(configuration, paymentMethodResponse.sessionInformation, componentOptions.style, locale) :
              componentOptions;

            if (paymentMethod.addSourceDataToOptions) {
              addSourceDataToOptions(configuration, componentOptionsOrPaymentRequest, paymentMethod, controller, availablePaymentMethod, paymentMethodResponse.sessionInformation, locale);
            }

            if (paymentMethod.hasLogos) {
              const filter = typeof paymentMethodResponse.sessionInformation !== 'undefined' ? paymentMethodResponse.sessionInformation.currency : undefined;
              componentOptionsOrPaymentRequest.logos = filterLogosByCurrency(getLogoDetails(), filter);
            }

            if (paymentMethod.requiresSessionInformation) {
              componentOptionsOrPaymentRequest[paymentMethod.type] = {
                country: paymentMethodResponse.sessionInformation.country,
                currency: paymentMethodResponse.sessionInformation.currency
              };
            }

            if (shouldUseDefaultStyles(paymentMethod, componentOptionsOrPaymentRequest, configuration)) {
              componentOptionsOrPaymentRequest.style = configuration.paymentMethodConfiguration.style;
            }

            const events = Object.assign({}, componentOptions.events);
            delete componentOptionsOrPaymentRequest.events;

            const component = createElement(paymentMethod.code, componentOptionsOrPaymentRequest);
            if (typeof component.canMakePayment !== 'undefined' && !component.canMakePayment()) {
              clearComponentFromDOM(parent, headerId, bodyId);
              availablePaymentMethod.addedToDOM = false;
              return;
            }

            if (typeof configuration.onReady !== 'undefined') {
              addReadyEventToComponents(component, componentsReadyStatus, configuration.onReady, paymentMethodResponse);
            }

            addClientEventsToComponent(events, component);

            component.mount(componentHolder.id);
            componentsMounted[paymentMethod.code] = component;

            if (paymentMethod.standaloneButton) {
              addStandaloneButtonOptions(component, configuration, paymentMethod, availablePaymentMethod);
            }

            if (requiresUpdateToInternalSourceData(paymentMethod, configuration, availablePaymentMethod) ) {
              getMandateCheckboxDOMElement(paymentMethod).addEventListener('change', function () {
                component.update({
                  style: {},
                  sourceData: createSourceData(configuration, paymentMethod, controller.id, availablePaymentMethod, paymentMethodResponse.sessionInformation, locale)
                });
              });
            }

          } catch (e) {
            clearComponentFromDOM(parent, headerId, bodyId);
            availablePaymentMethod.addedToDOM = false;
          }

        }
      });

      if (paymentMethodResponse.paymentMethods.length > 0 && (allPaymentMethodsFailedToAdd(paymentMethodResponse) || onlyButtonsWereAdded(componentsReadyStatus))) {
        checkAndSendOnReadyEvent(configuration, paymentMethodResponse);
      }

      return paymentMethodResponse;

    }).then((paymentMethodResponse) => {
      if (paymentMethodsReturned(paymentMethodResponse) && getShowCompliance(configuration) && paymentMethodsAdded(paymentMethodResponse)) {
        handleLegalFooter(paymentMethodResponse, getComplianceDetails, locale, accordion);
      }
    }).then(expandFirstPaymentMethod);

  };


}

function getComplianceData(getComplianceDetails, paymentMethodResponse, locale) {
  try {
    return getComplianceDetails(paymentMethodResponse.sessionInformation.sellingEntityId, locale, paymentMethodResponse.sessionInformation.country);
  } catch (e) {
    return undefined;
  }
}

function handleLegalFooter(paymentMethodResponse, getComplianceDetails, locale, accordion) {
  if (typeof paymentMethodResponse.sessionInformation !== 'undefined' && typeof paymentMethodResponse.sessionInformation.sellingEntityId !== 'undefined') {

    const compliance = getComplianceData(getComplianceDetails, paymentMethodResponse, locale);

    if (typeof compliance === 'undefined' || typeof compliance.disclosure === 'undefined') {
      return;
    }

    const legalFooter = document.createElement('div');
    legalFooter.className = 'DR-Legal-Footer';

    if (typeof compliance.disclosure.resellerDisclosure !== 'undefined') {
      const resellerDisclosure = document.createElement('div');
      resellerDisclosure.innerHTML = compliance.disclosure.resellerDisclosure.localizedText;
      legalFooter.appendChild(resellerDisclosure);
    }

    const legalFooterLinks = document.createElement('div');
    legalFooterLinks.className = 'DR-Legal-Footer-Links';

    createAndAddLink(compliance.disclosure.privacyPolicy, legalFooterLinks);
    createAndAddLink(compliance.disclosure.termsOfSale, legalFooterLinks);
    createAndAddLink(compliance.disclosure.californiaPrivacyRights, legalFooterLinks);
    createAndAddLink(compliance.disclosure.cookiePolicy, legalFooterLinks);
    createAndAddLink(compliance.disclosure.cancellationRights, legalFooterLinks);
    createAndAddLink(compliance.disclosure.legalNotice, legalFooterLinks);
    createAndAddLink(compliance.disclosure.warrantyInformation, legalFooterLinks);

    legalFooter.appendChild(legalFooterLinks);
    accordion.appendChild(legalFooter);
  }
}

function createAndAddLink(legalLink, legalFooterLinks) {

  if (typeof legalLink === 'undefined') {
    return;
  }

  const linkContainer = document.createElement('span');

  const link = document.createElement('a');
  link.className = 'DR-Legal-Footer-Link';
  link.href = legalLink.url;
  link.innerText = legalLink.localizedText;
  link.target = '_blank';


  linkContainer.appendChild(link);
  legalFooterLinks.appendChild(linkContainer);
}

function expandFirstPaymentMethod() {
  const expandButtons = document.querySelectorAll('.DR-button');
  if (expandButtons.length > 0) {
    document.querySelectorAll('.DR-button')[0].click();
  }
}

export function createHeaderDiv(paymentMethod, availablePaymentMethod, headerId, bodyId) {

  const headerDiv = document.createElement('div');
  headerDiv.className = 'DR-card-header';
  headerDiv.id = headerId;

  const header = document.createElement('h5');

  const headerButton = document.createElement('button');
  headerButton.className = 'DR-button';
  headerButton.setAttribute('data-DR-toggle', 'collapse');
  headerButton.setAttribute('data-DR-target', bodyId);
  headerButton.setAttribute('aria-expanded', 'false');
  headerButton.setAttribute('type', 'button');
  headerButton.addEventListener('click', (event) => handleHeaderButtonClick(event));

  const paymentMethodName = document.createElement('span');
  if (typeof availablePaymentMethod.paymentName !== 'undefined') {
    paymentMethodName.innerText = availablePaymentMethod.paymentName;
  } else {
    paymentMethodName.innerText = paymentMethod.name;
  }
  paymentMethodName.className = 'DR-payment-method-name';


  const defaultLogo = typeof availablePaymentMethod.images !== 'undefined' && typeof availablePaymentMethod.images.iconImage !== 'undefined' ? availablePaymentMethod.images.iconImage : '';

  const whiteLabelLogo = typeof availablePaymentMethod.paymentIcon !== 'undefined' ? availablePaymentMethod.paymentIcon : '';

  const selectedLogoURL = whiteLabelLogo.length > 0 ? whiteLabelLogo : defaultLogo;

  if (typeof selectedLogoURL !== 'undefined' && selectedLogoURL.length > 0) {
    headerButton.className =  headerButton.className + ' DR-logo-button';

    const image = document.createElement('img');
    image.src = selectedLogoURL;
    image.alt = paymentMethod.name;
    image.className = 'DR-payment-method-logo';

    paymentMethodName.className = paymentMethodName.className + ' DR-payment-method-name-with-image';

    headerButton.appendChild(image);
  }

  headerButton.appendChild(paymentMethodName);

  header.appendChild(headerButton);
  headerDiv.appendChild(header);
  return headerDiv;
}

function createComponentContainer(paymentMethod, availablePaymentMethod, parent, bodyId, headerId, componentId, shouldCreateBody, accordionId, configuration, sessionInformation) {

  const headerDiv = createHeaderDiv(paymentMethod, availablePaymentMethod, headerId, bodyId);
  parent.appendChild(headerDiv);

  const bodyParent = document.createElement('div');
  bodyParent.className = 'DR-collapse';
  bodyParent.id = bodyId;
  bodyParent.setAttribute('aria-labelledby', bodyId);
  bodyParent.setAttribute('data-parent', '#' + accordionId);

  const componentHolder = document.createElement('div');
  componentHolder.id = componentId;

  if (paymentMethod.standaloneButton && (isTermsOfSaleDisclosureMode(configuration, sessionInformation) || isManagePaymentMethodsMode(configuration))) {
    componentHolder.classList.add('DR-prevent-click');
  }

  if (shouldCreateBody) {
    const cardBody = document.createElement('div');
    cardBody.className = 'DR-card-body';

    const row = document.createElement('div');
    row.className = 'DR-row';

    row.appendChild(componentHolder);
    cardBody.appendChild(row);
    bodyParent.appendChild(cardBody);
  }

  parent.appendChild(bodyParent);

  return {bodyParent, componentHolder};
}

function getTypeData(configuration, controllerId, availablePaymentMethod) {

  if (availablePaymentMethod.flow === 'redirect') {
    return {
      'returnUrl' : isRedirectDisabled(configuration) && isRedirectReturnUrlProvided(configuration) ? getReturnUrl(configuration) : getRedirectReceiverURL(controllerId) + '&action=return',
      'cancelUrl' : isRedirectDisabled(configuration) && isRedirectCancelUrlProvided(configuration) ? getCancelUrl(configuration) : getRedirectReceiverURL(controllerId) + '&action=cancel',
    };
  } else {
    return {};
  }
}

function sessionIsRecurring(sessionInformation) {
  return typeof sessionInformation !== 'undefined' && sessionInformation.recurring;
}

function createSourceData(configuration, paymentMethod, controllerId, availablePaymentMethod, sessionInformation, locale) {

  const sourceData = {
    owner: typeof configuration.billingAddress !== 'undefined' ? configuration.billingAddress : null,
    [paymentMethod.type]: getTypeData(configuration, controllerId, availablePaymentMethod),
    type: paymentMethod.type,
    sessionId: configuration.sessionId,
    currency: typeof sessionInformation !== 'undefined' ? sessionInformation.currency : getCurrencyFromCountry(configuration.billingAddress.address.country),
    [LOGGING_APPLICATION_KEY]: 'dropin'
  };

  if(isManagePaymentMethodsMode(configuration)) {
    sourceData.mandate = createMandateSourceObject(locale);
    sourceData.futureUse = true;
  } else if (displayShowTermsOfSaleDisclosure(configuration, sessionInformation) && sessionIsRecurring(sessionInformation)) {
    sourceData.mandate = createTermsSourceObject(locale, sessionInformation);
    sourceData.futureUse = true;
  } else if(!isManagePaymentMethodsMode(configuration) && displaySavePaymentAgreement(configuration, availablePaymentMethod)) {
    const isChecked = getMandateCheckboxValue(paymentMethod);
    if(isChecked) {
      sourceData.mandate = createMandateSourceObject(locale);
      sourceData.futureUse = true;
    }
  }

  if(sourceData.futureUse && getUsage(configuration) !== null) {
    sourceData.usage = getUsage(configuration);
  }
  return sourceData;
}

function createMandateSourceObject(locale) {
  return {
    terms: getMandateValue(locale)
  };
}

function createActiveAcceptance(paymentMethod, configuration, locale, sessionInformation) {
  const buttonText = determineButtonConfigurationText(configuration, locale);
  const createAccountButtonTextClass = addMSTSEnrollmentURL(paymentMethod, configuration) ? 'DR-msts-create-account' : 'DR-msts-create-account-hidden';
  const businessEntityId = typeof sessionInformation !== 'undefined' && typeof sessionInformation.sellingEntityId !== 'undefined' ? sessionInformation.sellingEntityId : undefined;
  const activeAcceptance = document.createElement('p');
  activeAcceptance.className = 'DR-active-acceptance';
  activeAcceptance.innerHTML = getActiveAcceptance(locale, businessEntityId, paymentMethod.type, buttonText, createAccountButtonTextClass);
  return activeAcceptance;
}

function getMandateValue(locale) {
  return getSaveCardMandateText(locale);
}

function createMandate(paymentMethod, locale, componentHolder, isStorageMode) {

  const paymentMethodType = paymentMethod.type;

  const mandateParent = document.createElement('div');
  mandateParent.className = 'DR-mandate DR-mandate-' + paymentMethodType;

  const mandateError = document.createElement('div');
  mandateError.id = 'DR-mandate-checkbox-error-' + paymentMethodType;
  mandateError.className = 'DR-mandate-checkbox-error DR-error-message';

  const mandateRow = document.createElement('div');
  mandateRow.className = 'DR-mandate-row DR-mandate-row-' + paymentMethodType;
  const mandateText = document.createElement('div');

  const mandateCheckboxParent = document.createElement('div');
  const mandateCheckbox = document.createElement('input');
  mandateCheckbox.id = 'DR-mandate-checkbox-' + paymentMethodType;
  mandateCheckbox.type = 'checkbox';

  mandateCheckboxParent.className = 'DR-mandate-checkbox DR-mandate-checkbox-' + paymentMethodType;
  mandateCheckboxParent.appendChild(mandateCheckbox);

  mandateRow.appendChild(mandateCheckboxParent);

  mandateText.className = 'DR-mandate-content DR-mandate-'+paymentMethodType;
  mandateText.innerHTML = getMandateValue(locale);

  mandateRow.appendChild(mandateText);

  mandateParent.appendChild(mandateRow);
  mandateParent.appendChild(mandateError);

  if (isStorageMode) {
    addChangeEventToCheckbox(mandateCheckbox, paymentMethod, componentHolder);
  }

  return mandateParent;
}

function getMandateCheckboxValue(paymentMethod) {
  return document.getElementById('DR-mandate-checkbox-' + paymentMethod.type).checked;
}

function getShowCompliance(configuration) {
  return typeof configuration.options !== 'undefined' && typeof configuration.options.showComplianceSection !== 'undefined' ? configuration.options.showComplianceSection : true;
}

function createSubmitButton(controllerId, configuration, createSource, componentsMounted, paymentMethod, submitButtonId, paymentMethodFromAPI, sessionInformation, locale) {

  const spinnerAltText = getLocaleMessage(locale, 'processingYourRequest');
  const requireStorageValidation = isManagePaymentMethodsMode(configuration);
  const showTermsOfSaleDisclosure = isTermsOfSaleDisclosureMode(configuration, sessionInformation);
  const selectedText = getButtonText(configuration, locale);

  const submitButton = document.createElement('button');
  submitButton.id = submitButtonId;
  submitButton.className = 'DR-pay-button DR-button-text';
  submitButton.type = 'submit';
  submitButton.innerText = selectedText;
  submitButton.onclick = function (event) {
    event.preventDefault();
    const sourceData = createSourceData(configuration, paymentMethod, controllerId, paymentMethodFromAPI, sessionInformation, locale);

    if (requireStorageValidation) {

      const isChecked = getMandateCheckboxValue(paymentMethod);
      const errorMessage = document.getElementById('DR-mandate-checkbox-error-'+paymentMethod.type);
      if (isChecked === false) {
        errorMessage.innerHTML = getMustAcceptTermsText(locale);
        return;
      } else {
        errorMessage.innerHTML = '';
      }

    }

    if (showTermsOfSaleDisclosure) {

      const isChecked = getTermsCheckboxValue(paymentMethod);
      const errorMessage = document.getElementById('DR-terms-checkbox-error-'+paymentMethod.type);
      if (isChecked === false) {
        errorMessage.innerHTML = getMustAcceptTermsText(locale);
        return;
      } else {
        errorMessage.innerHTML = '';
      }

    }


    updateButton(event.target, createCssSpinner(spinnerAltText), true);

    if(paymentMethod.justRequiresSource && paymentMethod.onlyButton) {
      createSource(sourceData).then((response) => {
        runClientProvidedCompleteEvents(configuration, response, submitButton, selectedText, paymentMethodFromAPI);
      });
    } else if(paymentMethodFromAPI.flow === 'redirect') {
      handleRedirectSource(controllerId, configuration, paymentMethod, createSource, sourceData, componentsMounted, selectedText, paymentMethodFromAPI);
    } else {
      createSource(componentsMounted[paymentMethod.code], sourceData).then((response) => {
        runClientProvidedCompleteEvents(configuration, response, submitButton, selectedText, paymentMethodFromAPI);
      });
    }
  };

  return submitButton;
}

function findOptionsKey(configuration, paymentMethod) {

  if (typeof configuration === 'undefined') {
    return;
  }

  return Object.keys(configuration).find(option => option.toLowerCase() === paymentMethod.code);
}

function addReadyEventToComponents(component, componentsReadyStatus, readyEvent, paymentMethodResponse) {
  componentsReadyStatus[component.type] = 'NOT_READY';

  component.on('ready', function () {
    componentsReadyStatus[component.type] = 'READY';
    if (areAllComponentsReady(componentsReadyStatus)) {
      readyEvent(createOnReadyResponse(paymentMethodResponse));
    }
  });
}

function areAllComponentsReady(componentsReadyStatus) {
  return Object.keys(componentsReadyStatus).filter(key => componentsReadyStatus[key] === 'NOT_READY').length === 0;
}

function addClientEventsToComponent(events, component) {
  if (typeof events !== 'undefined' && Object.keys(events).length > 0) {
    Object.keys(events).forEach(event => {
      component.on(event, events[event]);
    });
  }
}

function clearComponentFromDOM(parent, headerId, bodyId) {
  const header = document.getElementById(headerId);
  const body = document.getElementById(bodyId);
  header.parentNode.removeChild(header);
  body.parentNode.removeChild(body);
  parent.parentNode.removeChild(parent);
}

export function handleRedirectSource(controllerId, configuration, paymentMethod, createSource, clientSourceData, componentsMounted, selectedText, paymentMethodFromAPI) {

  const createSourceFunction = paymentMethod.onlyButton ? createSource(clientSourceData) : createSource(componentsMounted[paymentMethod.code], clientSourceData);
  const redirectDisabled = isRedirectDisabled(configuration);
  const submitButton = getSubmitButtonByPaymentMethodType(paymentMethod.type);

  const redirectWindow = openWindow('');

  return createSourceFunction.then((response) => {
    if (response.source !== null && !redirectDisabled) {
      redirectWindow.location.replace(response.source.redirect.redirectUrl);
      sendRedirectSourceData(controllerId, response.source.id, response.source.clientSecret.split('_')[1], redirectWindow, paymentMethod.type);
    } else {
      runClientProvidedCompleteEvents(configuration, response, submitButton, selectedText, paymentMethodFromAPI);
    }
  }).catch((error) => {
    runClientProvidedCompleteEvents(configuration, error, submitButton, selectedText, paymentMethodFromAPI);
    redirectWindow.close();
  });


}

function handleHeaderButtonClick(event) {
  event.preventDefault();
  event.stopPropagation();

  const determinedTarget = typeof event.target.type === 'undefined' ? event.target.parentElement : event.target;

  const toggleTarget = document.getElementById(determinedTarget.getAttribute('data-DR-target'));
  const isExpanded = determinedTarget.getAttribute('aria-expanded');

  if(isExpanded === 'false') {
    const allTargets = [].slice.call(document.querySelectorAll('[data-DR-toggle]'));
    allTargets.forEach((target) => {
      target.setAttribute('aria-expanded', 'false');
      document.getElementById(target.getAttribute('data-DR-target')).classList.remove('DR-show');
    });
    determinedTarget.setAttribute('aria-expanded', 'true');
    toggleTarget.classList.add('DR-show');
  } else {
    determinedTarget.setAttribute('aria-expanded', 'false');
    toggleTarget.classList.remove('DR-show');
  }

}

