import {getComponentWindow} from './createComponent';
import postRobot from '../post-robot-wrapper';

export function getPaymentMethods(controllerId, sessionId, country, currency, locale, supportsStorage, recurringModel) {

  const controllerWindow = getComponentWindow(controllerId);

  if (!controllerWindow) {
    throw new Error(`Unable to locate controller '${controllerId}'`);
  }

  // Send message to Controller Frame to call createSource
  return postRobot.send(controllerWindow, 'fetchAvailablePaymentMethods', {
    sessionId: sessionId,
    country: country,
    currency: currency,
    locale: locale,
    supportsStorage: supportsStorage,
    recurringModel: recurringModel
  })
    .then(response => {
      return response.data;
    });

}
